// В этом файле должны быть стили для БЭМ-блока modal, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.modal {

  $block-name:                &; // #{$block-name}__element

  .modal-dialog {
    padding-right: 30px;
    @include media-breakpoint-up(sm) {
      max-width: 555px;
      padding-right: 0;
    }
  }

//   @media (min-width: 576px)
// <style>…</style>
// .modal-dialog {
//     max-width: 500px;
//     margin: 1.75rem auto;
// }

  .modal-content {
    // padding: 0 3.5rem;

    @include media-breakpoint-up(sm) {
      padding: 0 3.5rem;
    }
  }

  .modal-header {
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-bottom: 0;
    padding-bottom: 0.5rem;

    .close {
      position: absolute;
      right: -30px;
      top: 15px;
      opacity: 1;
      width: 32px;
      height: 32px;
      transition: opacity $transition-time;

      > span {
        &:before,
        &:after {
          background: #fff;
          height: 3px;
        }
      }

      &:hover,
      &:focus {
        opacity: 0.7;
        outline: none;
        box-shadow: none;
      }
    }
  }

  .modal-title {
    color: #0a182c;
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 0.6rem;
  }
  .modal-sub-title {
    color: #525c6d;
    margin-bottom: 0;
  }

  .form-group {
    .form-group-text {
      font-size: 14px;
      color: #98a3b5;
      margin-bottom: 0.5rem;
    }
  }

  .form-control {
    border-color: #e8ecf3;
    background: #e8ecf3;
    color: #525c6d;

    &::placeholder {
      color: #525c6d;
    }
  }

  .custom-control {
    font-size: 14px;
    color: #98a3b5;

    a {
      color: #98a3b5;
      text-decoration: underline;

      &:hover,
      &:focus {
        text-decoration: none;
      }
    }
  }

  .custom-file {
    max-width: 200px;

    input,
    label {
      cursor: pointer;
    }

    &:hover,
    &:focus {
      cursor: pointer;
    }
  }
  .custom-file-input {


    &:hover,
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }
  .custom-file-label {

    border: none;
    padding-left: 0;
    font-size: 14px;

    svg {
      margin-right: 10px;
      fill: #1b66d6;
    }

    span {
      color: #1b66d6;
      text-decoration: underline;
    }

    &:after {
      display: none;
    }
  }

}
