// В этом файле должны быть стили для БЭМ-блока page-header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-header {

  $block-name:                &; // #{$block-name}__element

  background: #fff;
  position: relative;
  z-index: 2;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);

  .burger {
    margin-left: auto;
  }


  .top-header {
    padding: 18px 0;
    border-bottom: 1px solid #e7e7e7;

    .container-fluid {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  .container-fluid {

    @include media-breakpoint-up(xl) {
      padding-left: 40px;
      padding-right: 40px;
    }
  }

  .header-info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @include media-breakpoint-down(md) {
      max-width: 100%;
      flex-basis: 100%;
    }


    &__contacts {

      .item {
        color: inherit;
        font-size: 17px;
        font-weight: 500;
        margin-right: 30px;
        font-weight: 500;
        white-space: nowrap;
        margin-top: 5px;
        margin-bottom: 5px;
        display: inline-block;

        svg {
          fill: #11b89e;
          margin-right: 10px;
          margin-bottom: 3px;
        }

        &:not(:first-child) {
          @include media-breakpoint-down(md) {
            display: none;
          }
        }

        &:hover,
        &:focus {
          text-decoration: none;
        }
      }
    }

    &__sign-up {

      @include media-breakpoint-down(md) {
        display: none;
      }

      .btn-outline-secondary {
        color: #000;
        font-weight: 700;
        padding-left: 1.8rem;
        padding-right: 1.8rem;
        svg {
          margin-right: 10px;
          fill: #11b89e;
        }

        &:hover,
        &:focus {
          color: #fff;
          border-color: #11b89e;
          background: #11b89e;
          svg {
            fill: #fff;
          }
        }
      }
    }
  }

  &__search {

    @media (min-width: 1365px) {
      min-width: 390px;
      // padding-left: 30px;
    }

    @include media-breakpoint-up(lg) {
      border-left: 1px solid #e7e7e7;
      height: 59px;
    }

    .search-form__input {
      border:none;
    }
  }
}
