.logo {

  white-space: nowrap;
  &:hover,
  &:focus {
    text-decoration: none;
  }

  &__text {
    font-family: 'PT Sans Caption', sans-serif;
    line-height: 1;
    margin: 0;
    font-size: 36px;
    font-weight: bold;
  }

  &__text-sub {
    font-size: 14px;
    color: #949494;
    margin: 0;
  }
}

.jumbotron {

  padding: 1.2rem 1rem;

  @include media-breakpoint-up(sm) {
    padding: 1.5rem 2rem;
  }

  p {
    font-size: 16px;
    line-height: 30px;
  }

  &--primary {
    background: #1b66d6;
    color: #fff;
  }
}

.list-custom {
  padding-left: 0;
  list-style: none;

  li {
    position: relative;
    padding-left: 14px;
    margin-bottom: 6px;

    &:after {
      content: "";
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background: #1b66d6;
      position: absolute;
      left: 0;
      top: 10px;
    }
  }

  &--xl {
    li {
      padding-left: 20px;
      font-weight: 500;
      &:after {
        top: 8px;
        width: 6px;
        height: 6px;
      }
    }
  }
}

.custom-nav {
  padding-top: 0;
  padding-bottom: 0;

  @include media-breakpoint-up(xl) {
    padding-left: 40px;
    padding-right: 40px;
  }

  .navbar-collapse {
    background: #fff;
  }

  .navbar-nav {
    .nav-link {
      color: inherit;
      // padding: 1.1rem 17px !important;

      @include media-breakpoint-up(lg) {
        font-size: 14px;
        padding: 1.1rem 10px !important;
      }

      @include media-breakpoint-up(xl) {
        font-size: 16px;
        padding: 1.1rem 17px !important;
      }

      &:hover,
      &:focus {
        color: #11b89e;
      }
    }

    .nav-item {
      &.active {
        .nav-link {
          color: #11b89e;
        }
      }
    }
  }

  .dropdown-direction {
    margin-right: 12px;
    .nav-link.dropdown-toggle {
      display: flex;
      align-items: center;
      font-weight: 700;
      padding-left: 0 !important;
      // padding: 1rem 2.4rem 1rem 0 !important;

      @media (min-width: 1365px) {
        padding-right: 1rem;
        padding: 1rem 2.4rem 1rem 0 !important;
        font-size: 18px;
      }

      @include media-breakpoint-up(lg) {

        &:before {
          content: "";
          border-right: 1px solid #e7e7e7;
          position: absolute;
          top: 0;
          bottom: 0;
          right: 0;
        }
      }

      &:after {
        display: none;
      }
    }
  }

  .logo,
  .navbar-toggler {
    display: none;
  }

  &--sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 20;
    background: #fff;
    animation: slideInDown 0.5s 1 ease-in-out;
    box-shadow: 0 0 13px 0 rgba(0, 0, 0, 0.29);

    .logo,
    .navbar-toggler {
      display: block;
    }

    .navbar-toggler {
      margin-top: -10px;
    }

    .logo {
      padding-top: 5px;
      padding-bottom: 5px;
      &__text {
        font-size: 30px;
      }
      &__text-sub {
        font-size: 14px;
      }
    }

    .navbar-nav.mr-auto {
      margin: auto !important;
    }

    .navbar-collapse.show {
      margin-right: -16px;
      margin-left: -15px;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}

@keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

$toggler-part-height:         2px;
$toggler-part-bg-color:       #11b89e;

.dropdown-burger {
  flex-shrink: 0;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  width: 20px;
  height: 18px;
  padding: 0;
  user-select: none;
  cursor: pointer;
  margin-right: 10px;
  outline: none !important;

  > span {
    display: inline-block;
    width: 100%;
    height: $toggler-part-height;
    background: $toggler-part-bg-color;
    position: relative;
    transition: background $transition-time $transition-time;

    &:before,
    &:after {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: $toggler-part-height;
      background: $toggler-part-bg-color;
      transform-origin: 50% 50%;
      transition: top $transition-time $transition-time, transform $transition-time;
    }

    &:before {
      top: -6px;
    }

    &:after {
      top: 6px;
    }
  }
}


