    margin-bottom: 1.7rem;
  }
}


.custom-control-label {
  color: #98a3b5;

  &:before {
    background: #c0cbdd;
    border-color: #c0cbdd;
  }

  a {
    text-decoration: underline;
    color: #98a3b5;

    &:hover {
      text-decoration: none;
      color: #11b89e;
    }
  }
}

.sidebar-nav {

  border: 1px solid #afc6d5;
  border-radius: 4px;
  overflow: hidden;

  >.nav-item:first-child {
    >.nav-link {
      border-top: 0;
    }
  }

  >.nav-item {
    >.nav-link {
      border-top: 1px solid #afc6d5;
      background-color: #fff;
      color: #000;
      padding: .9rem 1.3rem;
      text-transform: uppercase;
      font-size: 14px;
      font-weight: 600;
      margin-bottom: 0;
      font-size: 13px;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 13px;
        background-image: url("../img/icon-arrow-bottom.svg");
        background-repeat: no-repeat;
        width: 14px;
        height: 8px;
      }

      &[aria-expanded="true"]:after {
        background-image: url("../img/icon-arrow-top.svg");
      }
    }
  }

  .l_1 {
    >.nav > .nav-item > .nav-link {
      border-top: 1px solid #afc6d5;
      background-color: #e5f0f7;
      color: #000;
      padding: .7rem 1.75rem;
      font-size: 14px;
      margin-bottom: 0;
      position: relative;

      &:after {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 13px;
        background-image: url("../img/icon-arrow-bottom-gray.svg");
        background-repeat: no-repeat;
        width: 14px;
        height: 8px;
      }

      &[aria-expanded="true"]:after {
        background-image: url("../img/icon-arrow-top-gray.svg");
      }
    }
  }

  .l_2 {
    >.nav {
      padding-left: 1.4rem;
      padding-top: .6rem;
      padding-bottom: .9rem;
    }
    >.nav > .nav-item > .nav-link {
      text-decoration: underline;
      color: #1b66d6;
      font-size: 14px;
      padding: 0.25rem 1rem;

      &:hover,
      &:focus,
      &.active {
        color: #11b89e;
        text-decoration: none;
      }
    }
  }
}

.gallery-item {
  display: block;
  position: relative;
